import React, { useState, useEffect, useRef } from 'react';
import List from 'list.js';
import { Trash2, Plus, Search, ChevronsLeft, ChevronsRight, ChevronLeft, ChevronRight, XCircle } from 'lucide-react';

// Use JSDoc to define the Member type
/**
 * @typedef {object} Member
 * @property {number} id
 * @property {string} name
 * @property {string} email
 * @property {string} createdAt
 * @property {'Active' | 'Block'} status
 */

const initialMembers: Member[] = [
    { id: 1, name: 'Allie Grater', email: 'graterallie@gmail.com', createdAt: '2021-03-19', status: 'Block' },
    { id: 2, name: 'Rhoda Report', email: 'reportrhoda@gmail.com', createdAt: '2020-01-19', status: 'Active' },
    { id: 3, name: 'Rose Bush', email: 'rose@gmail.com', createdAt: '2020-10-26', status: 'Active' },
    { id: 4, name: 'Dave Allippa', email: 'dave@gmail.com', createdAt: '2020-06-19', status: 'Block' },
    { id: 5, name: 'Emma Grate', email: 'emma@gmail.com', createdAt: '2022-03-20', status: 'Block' },
    { id: 6, name: 'Mal Nurrisht', email: 'nurrishtmal@gmail.com', createdAt: '2019-12-29', status: 'Active' },
    { id: 7, name: 'Bess Twishes', email: 'twishes@gmail.com', createdAt: '2020-05-16', status: 'Block' },
    { id: 8, name: 'C. Yasoon', email: 'yasoon@gmail.com', createdAt: '2022-02-16', status: 'Active' },
    { id: 9, name: 'Teri Dactyl', email: 'teri@example.com', createdAt: '2021-08-01', status: 'Active' },
    { id: 10, name: 'Paige Turner', email: 'paige@example.com', createdAt: '2023-11-15', status: 'Block' },
    { id: 11, name: 'Artie Choke', email: 'artie@test.com', createdAt: '2022-09-05', status: 'Active' },
    { id: 12, name: 'Penny Wise', email: 'penny@money.com', createdAt: '2024-01-22', status: 'Active' },
];

// Simple Button Component
const Button = ({ variant, className, children, onClick, ...props }: any) => {
    let baseClass = 'px-4 py-2 rounded font-semibold focus:outline-none focus:ring-2 focus:ring-offset-2';

    if (variant === 'outline') {
        baseClass += ' border border-gray-300 bg-transparent text-gray-700 hover:bg-gray-100';
    } else if (variant === 'ghost') {
        baseClass += ' bg-transparent text-gray-700 hover:bg-gray-100';
    }
    else {
        baseClass += ' bg-blue-500 text-white hover:bg-blue-600'; // Default button style
    }

    const combinedClass = baseClass + ' ' + className;

    return (
        <button {...props} className={combinedClass} onClick={onClick}>
            {children}
        </button>
    );
};

// Simple Dialog Component
const Dialog = ({ open, onOpenChange, children }: any) => {
    if (!open) return null;

    return (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg shadow-xl p-4 w-full max-w-md">
                {children}
            </div>
            <button onClick={() => onOpenChange(false)} className="absolute top-2 right-2 text-gray-500 hover:text-gray-700">
                <XCircle className="h-6 w-6" />
            </button>
        </div>
    );
};

const DialogTrigger = ({ asChild, children, ...props }: any) => {
    if (asChild) {
        return <>{children}</>;
    }
    return (
        <div {...props}>
            {children}
        </div>
    )
};
const DialogContent = ({ className, children, ...props }: any) => {
    const combinedClass = "p-4 " + className;
    return (
        <div className={combinedClass} {...props}>
            {children}
        </div>
    )
};
const DialogHeader = ({ className, children, ...props }: any) => {
    const combinedClass = "space-y-1.5 " + className;
    return (
        <div className={combinedClass} {...props}>
            {children}
        </div>
    )
};
const DialogTitle = ({ className, children, ...props }: any) => {
    const combinedClass = "text-lg font-semibold " + className;
    return (
        <h2 className={combinedClass} {...props}>
            {children}
        </h2>
    )
};
const DialogDescription = ({ className, children, ...props }: any) => {
    const combinedClass = "text-sm text-gray-500 " + className;
    return (
        <p className={combinedClass} {...props}>
            {children}
        </p>
    )
};

const DialogFooter = ({ className, children, ...props }: any) => {
    const combinedClass = "flex justify-end " + className;

    return (
        <div className={combinedClass} {...props}>
            {children}
        </div>
    )
};

// Simple Input Component
const Input = React.forwardRef<HTMLInputElement, React.InputHTMLAttributes<HTMLInputElement>>(
    ({ className, type, ...props }, ref) => {
        const baseClass = "flex h-10 w-full rounded-md border border-gray-300 bg-white px-3 py-2 text-sm placeholder:text-gray-500 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50";
        const combinedClass = baseClass + ' ' + className;

        return <input type={type} className={combinedClass} ref={ref} {...props} />;
    }
);
Input.displayName = 'Input';

// Simple Label Component
const Label = React.forwardRef<HTMLLabelElement, React.LabelHTMLAttributes<HTMLLabelElement>>(
    ({ className, ...props }, ref) => {
        const baseClass = "text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70";
        const combinedClass = baseClass + ' ' + className;
        return (
            <label ref={ref} className={combinedClass} {...props} />
        );
    }
);
Label.displayName = "Label";

// Simple Select Component
const Select = ({ onValueChange, value, children, ...props }: any) => {
    const [selected, setSelected] = useState(value);

    const handleSelectChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
        const newValue = e.target.value;
        setSelected(newValue);
        onValueChange(newValue);
    };

    return (
        <div {...props}>
            <select
                value={selected}
                onChange={handleSelectChange}
                className="w-full rounded-md border border-gray-300 px-3 py-2 text-sm focus:outline-none focus-visible:ring-2 focus-visible:ring-gray-950 focus-visible:ring-offset-2"
            >
                {children}
            </select>
        </div>
    );
};

const SelectTrigger = ({ className, children, ...props }: any) => {
    const baseClass = "flex items-center justify-between rounded-md border border-gray-300 px-3 py-2 text-sm focus:outline-none focus-visible:ring-2 focus-visible:ring-gray-950 focus-visible:ring-offset-2";
    const combinedClass = baseClass + ' ' + className;
    return (
        <div {...props} className={combinedClass}>
            {children}
        </div>
    );
};

const SelectValue = ({ placeholder, children, className, ...props }: any) => {
    const baseClass = "truncate";
    const combinedClass = baseClass + ' ' + className;
    return (
        <span {...props} className={combinedClass}>
            {children || placeholder}
        </span>
    );
};

const SelectContent = ({ className, children, ...props }: any) => {
    const baseClass = "relative z-10 w-full overflow-auto rounded-md border bg-white text-sm shadow-md";
    const combinedClass = baseClass + ' ' + className;
    return (
        <div {...props} className={combinedClass}>
            {children}
        </div>
    );
};

const SelectItem = ({ value, children, onSelect, ...props }: any) => {
    const baseClass = "cursor-pointer px-3 py-2.5 hover:bg-gray-100";
    const combinedClass = baseClass + ' ' + props.className;
    return (
        <div {...props} value={value} className={combinedClass} onClick={() => {
            if (onSelect) {
                onSelect(value);
            }
        }}>
            {children}
        </div>
    );
};

const Table = ({ children, className, ...props }: any) => {
    const baseClass = "w-full border-collapse";
    const combinedClass = baseClass + ' ' + className;
    return (
        <table {...props} className={combinedClass}>
            {children}
        </table>
    );
};

const TableHeader = ({ children, className, ...props }: any) => {
    const baseClass = "";
    const combinedClass = className ? baseClass + ' ' + className : baseClass;
    return (
        <thead {...props} className={combinedClass}>
            {children}
        </thead>
    );
};

const TableBody = ({ children, className, ...props }: any) => {
    const baseClass = "";
    const combinedClass = className ? baseClass + ' ' + className : baseClass;
    return (
        <tbody {...props} className={combinedClass}>
            {children}
        </tbody>
    );
};

const TableRow = ({ children, className, ...props }: any) => {
    const baseClass = "";
    const combinedClass = className ? baseClass + ' ' + className : baseClass;
    return (
        <tr {...props} className={combinedClass}>
            {children}
        </tr>
    );
};

const TableCell = ({ children, className, ...props }: any) => {
    const baseClass = "px-4 py-2 border-b";
    const combinedClass = className ? baseClass + ' ' + className : baseClass;
    return (
        <td {...props} className={combinedClass}>
            {children}
        </td>
    );
};

const TableHead = ({ children, className, ...props }: any) => {
    const baseClass = "px-4 py-2 border-b font-semibold text-left";
    const combinedClass = className ? baseClass + ' ' + className : baseClass;
    return (
        <th {...props} className={combinedClass}>
            {children}
        </th>
    );
};

const MemberListTable = () => {
    const [members, setMembers] = useState<Member[]>(initialMembers);
    const [newMember, setNewMember] = useState<Omit<Member, 'id'>>({
        name: '',
        email: '',
        createdAt: '',
        status: 'Active',
    });
    const [open, setOpen] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 10;
    const [editMemberId, setEditMemberId] = useState<number | null>(null);
    const listRef = useRef<HTMLTableElement | null>(null);

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentMembers = members.slice(indexOfFirstItem, indexOfLastItem);

    const paginate = (pageNumber: number) => {
        if (pageNumber < 1) {
            pageNumber = 1;
        }
        if (pageNumber > totalPages) {
            pageNumber = totalPages;
        }
        setCurrentPage(pageNumber);
    };

    const totalPages = Math.ceil(members.length / itemsPerPage);

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setNewMember((prev) => ({
            ...prev,
            [name]: value,
        }));
    };

    const handleAddMember = () => {
        if (newMember.name && newMember.email && newMember.createdAt) {
            if (editMemberId) {
                setMembers(
                    members.map((member) =>
                        member.id === editMemberId
                            ? { ...member, ...newMember, id: editMemberId } // Keep the ID
                            : member
                    )
                );
                setEditMemberId(null);
            } else {
                const nextId = Math.max(0, ...members.map((m) => m.id)) + 1;
                setMembers([...members, { id: nextId, ...newMember }]);
            }

            setNewMember({ name: '', email: '', createdAt: '', status: 'Active' });
            setOpen(false);
            setCurrentPage(1);
        } else {
            alert('Please fill in all fields.');
        }
    };

    const handleDeleteMember = (id: number) => {
        setMembers(members.filter((member) => member.id !== id));
        setCurrentPage(1);
    };

    const handleEditMember = (member: Member) => {
        setNewMember({
            name: member.name,
            email: member.email,
            createdAt: member.createdAt,
            status: member.status,
        });
        setEditMemberId(member.id);
        setOpen(true);
    };

    const filteredMembers = currentMembers.filter((member) =>
        Object.values(member)
            .join(' ')
            .toLowerCase()
            .includes(searchQuery.toLowerCase())
    );

    const generatePageNumbers = () => {
        const pages = [];
        if (totalPages <= 7) {
            for (let i = 1; i <= totalPages; i++) {
                pages.push(i);
            }
        } else {
            if (currentPage <= 3) {
                pages.push(1, 2, 3, 4, 5, '...', totalPages);
            } else if (currentPage >= totalPages - 2) {
                pages.push(1, '...', totalPages - 4, totalPages - 3, totalPages - 2, totalPages - 1, totalPages);
            } else {
                pages.push(1, '...', currentPage - 1, currentPage, currentPage + 1, '...', totalPages);
            }
        }
        return pages;
    };

    useEffect(() => {
        if (listRef.current) {
            const options = {
                valueNames: ['id', 'name', 'email', 'createdAt', 'status', 'actions'],
            };
            const memberList = new List(listRef.current, options);

            return () => {
                memberList.destroy();
            };
        }
    }, [members]);

    return (
        <div className="p-4 md:p-6 lg:p-8">
            <div className="flex flex-col md:flex-row items-start md:items-center justify-between mb-4 md:mb-0">
                <h2 className="text-2xl font-semibold text-gray-800 mb-4 md:mb-0">Members</h2>
                <div className="flex flex-col md:flex-row gap-4 items-start md:items-center">
                    <Dialog open={open} onOpenChange={setOpen}>
                        <DialogTrigger asChild>
                            <Button variant="outline" className="bg-blue-500 hover:bg-blue-600 text-white flex items-center gap-2">
                                <Plus className="w-4 h-4" /> Add Member
                            </Button>
                        </DialogTrigger>
                        <DialogContent className="sm:max-w-[425px]">
                            <DialogHeader>
                                <DialogTitle>{editMemberId ? 'Edit Member' : 'Add Member'}</DialogTitle>
                                <DialogDescription>
                                    Fill out the form below to {editMemberId ? 'update' : 'add'} a new member.
                                </DialogDescription>
                            </DialogHeader>
                            <div className="grid gap-4 py-4">
                                <div className="grid grid-cols-4 items-center gap-4">
                                    <Label htmlFor="name" className="text-right">
                                        Name
                                    </Label>
                                    <Input
                                        id="name"
                                        name="name"
                                        value={newMember.name}
                                        onChange={handleInputChange}
                                        className="col-span-3"
                                        required
                                    />
                                </div>
                                <div className="grid grid-cols-4 items-center gap-4">
                                    <Label htmlFor="email" className="text-right">
                                        Email
                                    </Label>
                                    <Input
                                        id="email"
                                        name="email"
                                        type="email"
                                        value={newMember.email}
                                        onChange={handleInputChange}
                                        className="col-span-3"
                                        required
                                    />
                                </div>
                                <div className="grid grid-cols-4 items-center gap-4">
                                    <Label htmlFor="createdAt" className="text-right">
                                        Created At
                                    </Label>
                                    <Input
                                        id="createdAt"
                                        name="createdAt"
                                        type="date"
                                        value={newMember.createdAt}
                                        onChange={handleInputChange}
                                        className="col-span-3"
                                        required
                                    />
                                </div>
                                <div className="grid grid-cols-4 items-center gap-4">
                                    <Label htmlFor="status" className="text-right">
                                        Status
                                    </Label>
                                    <Select
                                        onValueChange={(value) => {
                                            const statusValue = value === 'Active' || value === 'Block' ? value : 'Active';
                                            setNewMember((prev) => ({ ...prev, status: statusValue }));
                                        }}
                                        value={newMember.status}
                                    >
                                        <SelectTrigger className="col-span-3">
                                            <SelectValue placeholder="Select a status" />
                                        </SelectTrigger>
                                        <SelectContent>
                                            <SelectItem value="Active">Active</SelectItem>
                                            <SelectItem value="Block">Block</SelectItem>
                                        </SelectContent>
                                    </Select>
                                </div>
                            </div>
                            <DialogFooter>
                                <Button
                                    type="button"
                                    onClick={handleAddMember}
                                    className="bg-green-500 hover:bg-green-600 text-white"
                                >
                                    {editMemberId ? 'Update' : 'Add'}
                                </Button>
                            </DialogFooter>
                        </DialogContent>
                    </Dialog>
                    <div className="relative">
                        <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-gray-500" />
                        <Input
                            type="text"
                            placeholder="Search members..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="pl-8"
                        />
                    </div>
                </div>
            </div>

            <div className="rounded-md border">
                <Table ref={listRef as any} className="list">
                    <TableHeader>
                        <TableRow>
                            <TableHead className="w-[80px]">ID</TableHead>
                            <TableHead>Name</TableHead>
                            <TableHead>Email</TableHead>
                            <TableHead>Created At</TableHead>
                            <TableHead>Status</TableHead>
                            <TableHead className="text-right">Action</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody className="list">
                        {filteredMembers.map((member) => (
                            <TableRow key={member.id}>
                                <TableCell className="font-medium id">{member.id}</TableCell>
                                <TableCell className="name">{member.name}</TableCell>
                                <TableCell className="email">{member.email}</TableCell>
                                <TableCell className="createdAt">{member.createdAt}</TableCell>
                                <TableCell className="status">
                                    <span
                                        className={
                                            member.status === 'Active'
                                                ? 'px-2 py-1 rounded bg-green-100 text-green-800'
                                                : 'px-2 py-1 rounded bg-red-100 text-red-800'
                                        }
                                    >
                                        {member.status}
                                    </span>
                                </TableCell>
                                <TableCell className="text-right actions">
                                    <Button
                                        variant="outline"
                                        size="icon"
                                        onClick={() => handleEditMember(member)}
                                        className="bg-blue-500/20 text-blue-400 hover:bg-blue-500/30 hover:text-blue-300 mr-1" // Added mr-1
                                    >
                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-pencil-line"><path d="M9 13h-3c-.6 0-1-.4-1-1v-3"/><path d="M15 5v2a2 2 0 0 1-2 2h-2"/><polyline points="16 8 21 3 15 3 16 8"/><circle cx="8.5" cy="8.5" r="2.5"/><path d="M12 15v-2s0-2-2-2"/></svg>
                                    </Button>
                                    <Button
                                        variant="outline"
                                        size="icon"
                                        onClick={() => handleDeleteMember(member.id)}
                                        className="bg-red-500/20 text-red-400 hover:bg-red-500/30 hover:text-red-300"
                                    >
                                        <Trash2 className="h-4 w-4" />
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </div>

            {totalPages > 1 && (
                <div className="flex items-center justify-center mt-4 space-x-2">
                    <Button
                        variant="outline"
                        onClick={() => paginate(1)}
                        disabled={currentPage === 1}
                        className="hover:bg-gray-100"
                    >
                        <ChevronsLeft className="h-4 w-4" />
                    </Button>
                    <Button
                        variant="outline"
                        onClick={() => paginate(currentPage - 1)}
                        disabled={currentPage === 1}
                        className="hover:bg-gray-100"
                    >
                        <ChevronLeft className="h-4 w-4" />
                    </Button>
                    {generatePageNumbers().map((page, index) =>
                        page === '...' ? (
                            <span key={`ellipsis-${index}`} className="text-gray-500">
                                ...
                            </span>
                        ) : (
                            <Button
                                key={page}
                                variant={currentPage === page ? 'default' : 'outline'}
                                onClick={() => paginate(page)}
                                className={currentPage === page ? 'bg-blue-500 text-white hover:bg-blue-600' : 'hover:bg-gray-100'}
                            >
                                {page}
                            </Button>
                        )
                    )}
                    <Button
                        variant="outline"
                        onClick={() => paginate(currentPage + 1)}
                        disabled={currentPage === totalPages}
                        className="hover:bg-gray-100"
                    >
                        <ChevronRight className="h-4 w-4" />
                    </Button>
                    <Button
                        variant="outline"
                        onClick={() => paginate(totalPages)}
                        disabled={currentPage === totalPages}
                        className="hover:bg-gray-100"
                    >
                        <ChevronsRight className="h-4 w-4" />
                    </Button>
                </div>
)}
        </div>
    );
};

export default MemberListTable;
